<template>
  <div class="home">
    <h1 class="mb-4">AWS Glue to Azure Data Factory Migration Mapping Tool</h1>
    
    <div v-if="!report" class="upload-section">
      <div class="card">
        <div class="card-header bg-primary text-white">
          <h5 class="mb-0">Upload AWS Glue Files</h5>
        </div>
        <div class="card-body">
          <div class="mb-3">
            <label for="glue-config" class="form-label">AWS Glue Configuration (JSON)</label>
            <input 
              type="file" 
              id="glue-config" 
              class="form-control"
              accept=".json"
              @change="handleGlueConfigChange"
              :disabled="isLoading"
            />
            <div v-if="glueConfigName" class="mt-2 text-success">
              <small>Selected file: {{ glueConfigName }}</small>
            </div>
          </div>
          
          <div class="mb-3">
            <label for="glue-script" class="form-label">AWS Glue Script (Python)</label>
            <input 
              type="file" 
              id="glue-script" 
              class="form-control"
              accept=".py"
              @change="handleGlueScriptChange"
              :disabled="isLoading"
            />
            <div v-if="glueScriptName" class="mt-2 text-success">
              <small>Selected file: {{ glueScriptName }}</small>
            </div>
          </div>
          
          <button 
            @click="generateMapping" 
            class="btn btn-primary"
            :disabled="!hasFiles || isLoading"
          >
            <span v-if="isLoading" class="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
            {{ isLoading ? 'Generating...' : 'Generate Migration Mapping' }}
          </button>
        </div>
      </div>
    </div>
    
    <div v-if="isLoading" class="loading-section text-center my-5">
      <div class="spinner-border text-primary" role="status">
        <span class="visually-hidden">Loading...</span>
      </div>
      <p class="mt-3">Generating migration mapping report...</p>
      <p class="text-muted">This may take a few moments.</p>
    </div>
    
    <div v-if="report" class="report-section">
      <div class="d-flex justify-content-end mb-3">
        <button @click="resetForm" class="btn btn-secondary me-2">Upload New Files</button>
        <button @click="downloadReport" class="btn btn-primary">Download Report</button>
      </div>
      
      <div class="card">
        <div class="card-header bg-success text-white">
          <h5 class="mb-0">Migration Mapping Report</h5>
        </div>
        <div class="card-body">
          <div class="report-content" v-html="renderedReport"></div>
        </div>
      </div>
    </div>
    
    <div v-if="error" class="alert alert-danger mt-3">
      {{ error }}
    </div>
  </div>
</template>

<script>
import { marked } from 'marked';
import axios from 'axios';

export default {
  name: 'Home',
  data() {
    return {
      glueConfig: null,
      glueScript: null,
      glueConfigName: '',
      glueScriptName: '',
      report: null,
      isLoading: false,
      error: null
    };
  },
  computed: {
    hasFiles() {
      return this.glueConfig && this.glueScript;
    },
    renderedReport() {
      return this.report ? marked(this.report) : '';
    }
  },
  methods: {
    handleGlueConfigChange(event) {
      const file = event.target.files[0];
      if (file) {
        this.glueConfigName = file.name;
        this.glueConfig = file;
        this.error = null;
      }
    },
    handleGlueScriptChange(event) {
      const file = event.target.files[0];
      if (file) {
        this.glueScriptName = file.name;
        this.glueScript = file;
        this.error = null;
      }
    },
    async generateMapping() {
      if (!this.hasFiles) {
        this.error = "Please upload both AWS Glue configuration and script files.";
        return;
      }
      
      this.isLoading = true;
      this.error = null;
      
      try {
        const formData = new FormData();
        formData.append('glue_config', this.glueConfig);
        formData.append('glue_script', this.glueScript);
        
        const response = await axios.post('/api/generate-mapping', formData);
        this.report = response.data.report;
      } catch (error) {
        this.error = `Error generating report: ${error.response?.data?.detail || error.message}`;
      } finally {
        this.isLoading = false;
      }
    },
    resetForm() {
      this.glueConfig = null;
      this.glueScript = null;
      this.glueConfigName = '';
      this.glueScriptName = '';
      this.report = null;
      this.error = null;
      
      // Reset file input elements after Vue has updated the DOM
      this.$nextTick(() => {
        const glueConfigInput = document.getElementById('glue-config');
        if (glueConfigInput) {
          glueConfigInput.value = '';
        }
        const glueScriptInput = document.getElementById('glue-script');
        if (glueScriptInput) {
          glueScriptInput.value = '';
        }
      });
    },
    downloadReport() {
      if (!this.report) return;
      
      const blob = new Blob([this.report], { type: 'text/markdown' });
      const url = window.URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = 'aws-azure-migration-mapping.md';
      document.body.appendChild(a);
      a.click();
      window.URL.revokeObjectURL(url);
      document.body.removeChild(a);
    }
  }
};
</script>

<style scoped>
.report-content {
  max-height: 70vh;
  overflow-y: auto;
  padding: 1rem;
  background-color: #f8f9fa;
  border-radius: 0.25rem;
}

.report-content :deep(h1),
.report-content :deep(h2),
.report-content :deep(h3),
.report-content :deep(h4),
.report-content :deep(h5),
.report-content :deep(h6) {
  margin-top: 1.5rem;
  margin-bottom: 1rem;
}

.report-content :deep(table) {
  width: 100%;
  margin-bottom: 1rem;
  border-collapse: collapse;
}

.report-content :deep(table th),
.report-content :deep(table td) {
  padding: 0.5rem;
  border: 1px solid #dee2e6;
}

.report-content :deep(table th) {
  background-color: #f1f1f1;
}

.report-content :deep(pre) {
  background-color: #f1f1f1;
  padding: 1rem;
  border-radius: 0.25rem;
  overflow-x: auto;
}

.report-content :deep(code) {
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 0.875em;
}
</style>
